body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.landing-page {
  font-family: Arial, sans-serif;
  color: #333;
  text-align: center;
}

.header {
  padding: 2rem;
  background-color: #f7f7f7;
  margin-bottom: 2rem;
}

.header h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.header p {
  font-size: 1.5rem;
}

.hero-image {
  position: relative;
  height: 600px;
  margin-bottom: 2rem;
}

.hero-image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.services-section {
  background-color: #fff;
  padding: 2rem;
  margin-bottom: 2rem;
}

.services-section h2 {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.services-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.service-card {
  width: 30%;
  margin-bottom: 2rem;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 1rem;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

.service-card:hover {
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  transform: translateY(-5px);
}

.service-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.service-card h3 {
  font-size: 1.5rem;
  margin: 1rem;
}

.service-card p {
  font-size: 1rem;
  margin: 1rem;
  text-align: justify;
}

.contact-section {
  background-color: #f7f7f7;
  padding: 2rem;
  margin-bottom: 2rem;
}

.contact-section h2 {
  font-size: 2rem;
  margin-bottom: 2rem;
}

form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 1rem;
}

input[type="text"], input[type="email"], textarea {
  width: 100%;
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
  background-color: #f1f1f1;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 1rem;
}

textarea {
  resize: none;
  height: 150px;
}

button {
  background-color: #4CAF50;
  color: #fff;
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 1.2rem;
  cursor: pointer;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
}

button:hover {
  background-color: #3e8e41;
}
